/*******************************************************************************
 * Styles for the base elements of the theme.
 ******************************************************************************/

// Typography
p,
h1,
h2,
h3,
h4,
h5,
h6,
em,
div,
li,
span,
strong {
 color: var(--global-text-color);
}

hr {
 border-top: 1px solid var(--global-divider-color);
}

a,
table.table a {
 color: var(--global-theme-color);

 &:hover {
 color: var(--global-theme-color);
 text-decoration: underline;
 }

 &:hover:after :not(.nav-item.dropdown) {
 width: 100%;
 }
}

.table-dark {
 background-color: transparent;

 &.table-bordered {
 border: 1px solid var(--global-divider-color) !important;
 }
}

blockquote {
 background: var(--global-bg-color);
 border-left: 5px solid var(--global-theme-color);
 margin: 1.5em 0;
 padding: 1em;

 // avoid overflow
 max-width: 100%;
 white-space: normal;
 word-wrap: break-word;
 overflow-wrap: break-word;

 > :last-child,
 > :last-child *:last-child {
 margin-bottom: 0;
 }

 /* Tips, warnings, and dangers blockquotes */
 &.block-tip {
 border-color: var(--global-tip-block);
 background-color: var(--global-tip-block-bg);

 em,
 li,
 p,
 strong,
 span {
 color: var(--global-tip-block-text);
 }

 h1,
 h2,
 h3,
 h4,
 h5,
 h6 {
 color: var(--global-tip-block-title);
 }
 }

 &.block-warning {
 border-color: var(--global-warning-block);
 background-color: var(--global-warning-block-bg);

 em,
 li,
 p,
 strong,
 span {
 color: var(--global-warning-block-text);
 }

 h1,
 h2,
 h3,
 h4,
 h5,
 h6 {
 color: var(--global-warning-block-title);
 }
 }

 &.block-danger {
 border-color: var(--global-danger-block);
 background-color: var(--global-danger-block-bg);

 em,
 li,
 p,
 strong,
 span {
 color: var(--global-danger-block-text);
 }

 h1,
 h2,
 h3,
 h4,
 h5,
 h6 {
 color: var(--global-danger-block-title);
 }
 }
}

// Checklist

ul.task-list {
 list-style-type: none;
 /* Remove bullets from all levels */
}

ul.task-list>li ul {
 /* Nested lists within .task-list */
 padding-inline-start: 1.5rem;
}

ul.task-list input[type="checkbox"] {
 margin: 0.275rem 0.5rem 0.5rem -1rem;
 vertical-align: middle;
}

// Math

.equation {
 margin-bottom: 1rem;
 text-align: center;
}

// Caption

.caption {
 color: gray;
 font-size: 0.875rem;
 margin-top: 0.75rem;
 text-align: center;
}

// Card

.card {
 background-color: var(--global-card-bg-color);

 .card-img {
 width: 100%;
 padding-top: 1.25rem;
 }

 .card-title {
 color: var(--global-text-color);
 }

 .card-body {
 padding: 1.25rem 1.25rem 1.25rem 1.25rem;
 }
}

// Citation

.citation,
.citation-number {
 color: var(--global-theme-color);
}

// About - Bio
#bio {
 font-size: 1.3rem;
 color: var(--global-text-color);
}

// Profile

.profile {
 width: 100%;
 margin-bottom: 1rem;

 .more-info {
 margin-bottom: 5px;
 margin-top: 5px;
 font-family: monospace;

 p {
 display: inline-block;
 margin: 0;
 }
 }
}

.profile.float-right {
 margin-left: 1rem;
}

.profile.float-left {
 margin-right: 1rem;
}

// Profile: show profile pics on the right even on small screens
@media (min-width: 360px) {
 .profile {
 width: 30%;

 .address {
 p {
 display: block;
 }
 }
 }
}

.post-description {
 margin-bottom: 2rem;
 font-size: 0.875rem;

 a {
 color: inherit;

 &:hover {
 color: var(--global-theme-color);
 text-decoration: none;
 }
 }
}

// Navbar customization

.navbar {
 box-shadow: none;
 border-bottom: 1px solid var(--global-divider-color);
 background-color: var(--global-bg-color);
 opacity: 0.95;
}

/* Dropdown menu styling */
.navbar .dropdown-menu {
 background-color: var(--global-bg-color);
 border: 1px solid var(--global-divider-color);
}

.navbar .dropdown-menu a:not(.active) {
 color: var(--global-text-color);
}

.navbar .dropdown-menu a:hover {
 color: var(--global-hover-color);
}

.navbar .dropdown-menu .dropdown-divider {
 border-top: 1px solid var(--global-divider-color) !important;
}

.dropdown-item {
 color: var(--global-text-color);
}

.dropdown-item:hover {
 color: var(--global-hover-color);
 background-color: var(--global-bg-color);
}

/* Navbar light theme styling */
.navbar.navbar-light a:hover {
 text-decoration: none;
}

.navbar.navbar-light .navbar-brand {
 color: var(--global-text-color);
}

.navbar.navbar-light .navbar-nav .nav-item .nav-link {
 color: var(--global-text-color);
}

.navbar.navbar-light .navbar-nav .nav-item .nav-link:hover {
 color: var(--global-hover-color);
}

.navbar.navbar-light .navbar-nav .nav-item.active>.nav-link {
 background-color: inherit;
 font-weight: bolder;
 color: var(--global-theme-color);
}

.navbar.navbar-light .navbar-nav .nav-item.active>.nav-link:hover {
 color: var(--global-hover-color);
}

/* CSS-only Navbar Toggler (mobile) styling */
.navbar-toggler {
 display: inline-block;
}

.navbar-toggler .icon-bar {
 display: block;
 width: 22px;
 height: 2px;
 background-color: var(--global-text-color);
 border-radius: 1px;
 margin-bottom: 4px;
 transition: all 0.2s;
}

.navbar-toggler .top-bar {
 transform: rotate(45deg);
 transform-origin: 10% 10%;
}

.navbar-toggler .middle-bar {
 opacity: 0;
}

.navbar-toggler .bottom-bar {
 transform: rotate(-45deg);
 transform-origin: 10% 90%;
}

/* Hide the mobile nav menu by default on small screens */
@media (max-width: 576px) {
 #navbarNav {
 display: none;
 }

 .navbar-toggler-checkbox:checked~#navbarNav {
 display: block;
 }
}

/* Light toggle button styling */
#light-toggle {
 padding: 0;
 border: 0;
 background-color: inherit;
 color: var(--global-text-color);
}

#light-toggle:hover {
 color: var(--global-hover-color);
}

#light-toggle svg {
 width: 1.8rem;
 height: 1.8rem;
}

/* CSS-only Dropdown Toggle styling */
/* Hide dropdown menus by default */
.dropdown-menu {
 display: none;
}

/* Show dropdown menu when its checkbox is checked */
.dropdown-checkbox:checked+label+.dropdown-menu {
 display: block;
}

/* Ensure the dropdown toggle (label) shows a pointer cursor */
.nav-link.dropdown-toggle {
 cursor: pointer;
}

/* Toggler icon - always shows 3 bars */
.navbar-toggler .icon-bar {
 display: block;
 width: 22px;
 height: 2px;
 background-color: var(--global-text-color);
 border-radius: 1px;
 margin: 4px 0;
 transition: none;
}

// Social (bottom)

.social {
 text-align: center;

 .contact-icons {
 a {
 i::before {
 color: var(--global-text-color);
 transition-property: all 0.2s ease-in-out;
 }

 &:hover {
 i::before {
 color: var(--global-theme-color);
 }
 }
 }
 }
}

// Footer
footer.fixed-bottom {
 background-color: var(--global-footer-bg-color);

 .container {
 color: var(--global-footer-text-color);
 padding-top: 9px;
 padding-bottom: 8px;
 text-align: center;
 }

 a {
 color: var(--global-footer-link-color);

 &:hover {
 color: var(--global-theme-color);
 text-decoration: none;
 }
 }
}

footer.sticky-bottom {
 border-top: 1px solid var(--global-divider-color);
 padding-top: 20px;
 padding-bottom: 40px;
 font-size: 0.875rem;
 flex-shrink: 0;

 .container {
 text-align: center;
 }
}

// CV

.cv {
 margin-bottom: 40px;

 .card {
 background-color: var(--global-card-bg-color);
 border: 1px solid var(--global-divider-color);

 .list-group-item {
 background-color: inherit;
 border-color: var(--global-divider-color);

 .badge {
 color: var(--global-card-bg-color) !important;
 background-color: var(--global-theme-color) !important;
 }
 }
 }
}

.table-cv-map {
 background-color: transparent;
 border: none;
 color: var(--global-text-color);
}

.date-column {
 width: 75px;
 transform: translateX(-15px) translateY(-5px);
}

// Repositories

@media (min-width: 768px) {
 .repo {
 max-width: 50%;
 }
}

// Blog

.header-bar {
 border-bottom: 1px solid var(--global-divider-color);
 text-align: center;
 padding-top: 2rem;
 padding-bottom: 3rem;

 h1 {
 color: var(--global-theme-color);
 font-size: 5rem;
 }
}

.tag-category-list {
 border-bottom: 1px solid var(--global-divider-color);
 text-align: center;
 padding-top: 1rem;

 ul {
 justify-content: center;
 display: flow-root;

 p,
 li {
 list-style: none;
 display: inline-block;
 padding: 1rem 0.5rem;
 color: var(--global-text-color-light);
 }
 }
}

.post-title {
 mjx-container[jax="CHTML"][display="true"] {
 display: unset;
 }
}

.post-list {
 table {
 width: 100%;

 /* Default layout for larger screens */
 tr {
 display: table-row;
 }

 td,
 th {
 font-size: 0.9em;
 }
 }

 /* Responsive Design for Small Screens */
 @media screen and (max-width: 800px) {
 table {
 display: block;

 tr {
 display: block;
 }

 th {
 display: block;
 padding-bottom: 0;
 }

 td {
 display: block;
 padding-top: 0.2rem;
 }
 }
 }
}

.pagination {
 .page-item {
 .page-link {
 color: var(--global-text-color);
 padding: .75rem 1.15rem;

 &:hover {
 color: $black-color;
 }
 }

 &.active .page-link {
 color: $white-color;
 background-color: var(--global-theme-color);

 &:hover {
 background-color: var(--global-theme-color);
 }
 }
 }
}

// Distill

.distill {
 a:hover {
 border-bottom-color: var(--global-theme-color);
 text-decoration: none;
 }
}

// Projects

.projects {
 a {
 text-decoration: none;

 &:hover {
 .card-title {
 color: var(--global-theme-color);
 }
 }
 }

 .card {
 img {
 width: 100%;
 }
 }

 .col {
 padding-bottom: 1rem;
 }

 .grid-sizer,
 .grid-item {
 width: 250px;
 margin-bottom: 10px;
 }

 h2.category {
 color: var(--global-divider-color);
 border-bottom: 1px solid var(--global-divider-color);
 padding-top: 0.5rem;
 margin-top: 2rem;
 margin-bottom: 1rem;
 text-align: right;
 }
}

// Publications

.publications {
 margin-top: 2rem;

 h1 {
 color: var(--global-theme-color);
 font-size: 2rem;
 text-align: center;
 margin-top: 1em;
 margin-bottom: 1em;
 }

 h2 {
 margin-bottom: 1rem;

 span {
 font-size: 1.5rem;
 }
 }

 h2.bibliography {
 color: var(--global-divider-color);
 border-top: 1px solid var(--global-divider-color);
 padding-top: 1rem;
 margin-top: 2rem;
 text-align: right;
 }

 ol.bibliography {
 list-style: none;
 padding: 0;
 margin-top: 0;

 li {
 margin-bottom: 1rem;

 .abbr {
 margin-bottom: 0.5rem;

 abbr {
 display: inline-block;
 background-color: var(--global-theme-color);
 margin-bottom: 0.5rem;

 a {
 color: white;

 &:hover {
 text-decoration: none;
 }
 }
 }

 .award {
 color: var(--global-theme-color) !important;
 border: 1px solid var(--global-theme-color);
 }
 }

 .title {
 font-weight: bolder;
 }

 .author {
 a {
 border-bottom: 1px dashed var(--global-theme-color);

 &:hover {
 border-bottom-style: solid;
 text-decoration: none;
 }
 }

 >em {
 border-bottom: 1px solid;
 font-style: normal;
 }

 >span.more-authors {
 color: var(--global-text-color-light);
 border-bottom: 1px dashed var(--global-text-color-light);
 cursor: pointer;

 &:hover {
 color: var(--global-text-color);
 border-bottom: 1px dashed var(--global-text-color);
 }
 }
 }

 .badges {
 padding-bottom: 0.5rem;

 span {
 display: inline-block;
 color: $black-color;
 height: 100%;
 padding-right: 0.5rem;
 vertical-align: middle;

 &:hover {
 text-decoration: underline;
 }
 }
 }

