 // Fix mid screen size layout issue
 @media screen and (max-width: 800px) {
 .col {
 -ms-flex-preferred-size: 0;
 flex-basis: 0;
 -ms-flex-positive: 1;
 flex-grow: 1;
 max-width: 100%;
 }
 }
}

// Rouge Color Customization
figure.highlight {
 margin: 0 0 1rem;
}

pre {
 color: var(--global-theme-color);
 background-color: var(--global-code-bg-color);
 border-radius: 6px;
 padding: 6px 12px;

 pre,
 code {
 background-color: transparent;
 border-radius: 0;
 margin-bottom: 0;
 padding: 0;
 }
}

code {
 color: var(--global-theme-color);
 background-color: var(--global-code-bg-color);
 border-radius: 3px;
 padding: 3px 3px;
}

// Transitioning Themes
html.transition,
html.transition *,
html.transition *:before,
html.transition *:after {
 transition: all 750ms !important;
 transition-delay: 0 !important;
}

progress {
 /* Positioning */
 position: fixed;
 left: 0;
 top: 56px;
 z-index: 10;

 /* Dimensions */
 width: 100%;
 height: 1px;

 /* Reset the appearance */
 -webkit-appearance: none;
 -moz-appearance: none;
 appearance: none;

 /* Get rid of the default border in Firefox/Opera. */
 border: none;

 /* Progress bar container for Firefox/IE10 */
 background-color: transparent;

 /* Progress bar value for IE10 */
 color: var(--global-theme-color);
}

progress::-webkit-progress-bar {
 background-color: transparent;
}

progress::-webkit-progress-value {
 background-color: var(--global-theme-color);
}

progress::-moz-progress-bar {
 background-color: var(--global-theme-color);
}

.progress-container {
 width: 100%;
 background-color: transparent;
 position: fixed;
 top: 56px;
 left: 0;
 height: 5px;
 display: block;
}

.progress-bar {
 background-color: var(--global-theme-color);
 width: 0%;
 display: block;
 height: inherit;
}

/* Copy button */
.code-display-wrapper {
 position: relative;

 .copy {
 background: var(--global-card-bg-color);
 border-color: var(--global-bg-color);
 border-radius: 0.3rem;
 border-style: none;
 color: var(--global-text-color);
 font-size: medium;
 opacity: 0;
 position: absolute;
 right: 0.2rem;
 top: 0.2rem;
 }

 &:active .copy,
 &:focus .copy,
 &:hover .copy {
 color: var(--global-hover-color);
 opacity: 1;
 }
}

.btn-group.dropdown {
 .btn {
 box-shadow: none;
 -webkit-box-shadow: none;
 }

 .btn-secondary.dropdown-toggle {
 border: 1px solid var(--global-divider-color);

 .page-size {
 color: inherit;
 }

 &:not(.active) {
 background-color: var(--global-bg-color) !important;
 color: var(--global-text-color);
 }

 &:hover {
 background-color: var(--global-hover-color) !important;
 color: var(--global-hover-text-color) !important;
 }
 }

 .dropdown-menu {
 background-color: var(--global-bg-color);
 }

 .dropdown-item {
 background-color: var(--global-bg-color);
 color: var(--global-text-color);

 &:hover {
 color: var(--global-hover-color);
 }
 }

 .dropdown-item.active,
 .dropdown-item:active {
 background-color: var(--global-hover-color);
 color: var(--global-hover-text-color) !important;

 &:hover {
 color: var(--global-hover-text-color);
 }
 }
}

/* small screens */
@media (max-width: 576px) {

 /* override stickyness so that the navigation does not follow scrolling */
 nav[data-toggle="toc"] {
 visibility: hidden;
 height: 0;
 top: 0;
 }
}

.featured-posts {
 a {
 color: var(--global-text-color-light);
 text-decoration: none;

 .card-text {
 overflow: hidden;
 text-overflow: ellipsis;
 white-space: nowrap;
 }

 &:hover {
 color: var(--global-theme-color);

 .card-title {
 color: var(--global-theme-color);
 }
 }
 }

 .post-meta {
 color: var(--global-text-color-light);
 font-size: 0.875rem;
 margin-bottom: 0;
 padding-top: 0.5rem;
 }
}

.unloaded {
 display: none !important;
}

.medium-zoom-overlay,
.medium-zoom-image--opened {
 z-index: 999;
}

#toc-sidebar {
 z-index: 1;
}

.echarts {
 height: 400px;
 width: 100%;
}

.map {
 height: 400px;
 width: 100%;
}

swiper-container {
 --swiper-navigation-color: var(--global-theme-color);
 --swiper-pagination-color: var(--global-theme-color);
 --swiper-pagination-bullet-inactive-color: var(--global-text-color);
}

.ps-root {
 .ps-algorithm {
 margin: 0.8em 0;
 border-top: 3px solid var(--global-text-color);
 border-bottom: 2px solid var(--global-text-color);
 }

 .ps-algorithm.with-caption>.ps-line:first-child {
 border-bottom: 2px solid var(--global-text-color);
 }
}

// newsletter
.newsletter-form-container {
 margin-bottom: 20px;
}

.newsletter-form {
 display: flex;
 flex-direction: row;
 align-items: center;
 width: 100%;
}

.newsletter-form-input,
.bibsearch-form-input {
 color: var(--global-newsletter-text-color);
 background: var(--global-newsletter-bg-color);
 border: 1px solid var(--global-newsletter-text-color);
 outline: none;
 margin: 0px 10px 0px 0px;
 width: 100%;
 max-width: 350px;
 min-width: 100px;
 box-sizing: border-box;
 box-shadow: rgba(0, 0, 0, 0.05) 0px 1px 2px;
 border-radius: 6px;
 padding: 8px 12px;
}

.newsletter-form-input:focus {
 border-color: var(--global-theme-color) !important;
}

.newsletter-form-button {
 background: var(--global-theme-color);
 color: var(--global-bg-color);
 display: flex;
 width: min-content;
 max-width: 200px;
 white-space: nowrap;
 height: 38px;
 align-items: center;
 flex-direction: row;
 padding: 9px 17px;
 box-shadow: rgba(0, 0, 0, 0.05) 0px 1px 2px;
 border-radius: 6px;
 text-align: center;
 font-style: normal;
 font-weight: 500;
 line-height: 20px;
 border: none;
 cursor: pointer;
}

.newsletter-loading-button {
 background: var(--global-theme-color);
 color: var(--global-bg-color);
 display: none;
 width: min-content;
 max-width: 300px;
 white-space: nowrap;
 height: 38px;
 align-items: center;
 flex-direction: row;
 padding: 9px 17px;
 box-shadow: rgba(0, 0, 0, 0.05) 0px 1px 2px;
 border-radius: 6px;
 text-align: center;
 font-style: normal;
 font-weight: 500;
 line-height: 20px;
 border: none;
 cursor: pointer;
 margin-right: 20px;
}

.newsletter-success {
 color: var(--global-text-color);
 display: none;
 align-items: center;
 width: 100%;
}

.newsletter-error {
 color: var(--global-theme-color);
 display: none;
 align-items: center;
 width: 100%;
}

.newsletter-back-button {
 color: var(--global-theme-color);
 margin: 10px auto;
 text-align: center;
 display: none;
 background: transparent;
 border: none;
 cursor: pointer;
}

@media (max-width: 575px) {

 .newsletter-form-input,
 .newsletter-form-button,
 .newsletter-loading-button,
 .newsletter-success,
 .newsletter-error {
 font-size: 16px !important;
 }

 .newsletter-form-container {
 margin-right: 20px;
 margin-left: 20px;
 }
}

// popover is used for annotation in bib.liquid
.popover {
 background-color: var(--global-bg-color);
 border-color: var(--global-divider-color);

 .popover-header {
 color: var(--global-text-color); // Header text color
 border-bottom: 1px solid var(--global-divider-color);
 }

 .popover-body {
 color: var(--global-text-color); // Body text color
 }
}

.bs-popover-top {

 // arrow fill color
 .arrow::after {
 border-top-color: var(--global-bg-color);
 }

 // arrow border color
 .arrow:before {
 border-top-color: var(--global-divider-color);
 }
}

.bs-popover-bottom {

 // arrow fill color
 .arrow::after {
 border-bottom-color: var(--global-bg-color);
 }

 // arrow border color
 .arrow:before {
 border-bottom-color: var(--global-divider-color);
 }
}

// highlight-search-term
::highlight(search) {
 background-color: var(--global-theme-color);
 color: var(--global-text-color);
}

/*****************************************************************************
* Custom
*****************************************************************************/

// SVG icon
.wh1 {
 .icon {
 width: 1rem;
 height: 1rem;
 fill: var(--global-text-color);
 }
}

.wh2 {
 .icon {
 width: 1.6rem;
 height: 1.5rem;
 fill: var(--global-text-color);
 margin: 2px;
 }
}

// Fix image being too large
.post-content {
 img {
 max-width: 100%;
 }

 table {

 td,
 th {
 font-size: inherit;
 }
 }
}

// Fix word-break in footnotes
.footnotes {
 word-break: break-word;
 font-size: medium;
}

// Add a separator before footnotes
.footnotes::before {
 content: "";
 border: 1px solid var(--global-divider-color);
 margin-bottom: 1rem;
 border-top-style: none;
 border-top-color: currentcolor;
 display: block;
}

// Adjust strong font weight
strong {
 font-weight: bold;
}

// Override BootStrap setting
table {
 color: var(--global-text-color) !important;
}

// Figure center
figure {
 display: flex;
 justify-content: center;
 align-items: center;
 text-align: center;
 flex-direction: column;
}

// Proof end
.proof-end::after {
 content: "□";
 float: right;
}

// Center everything
.center {
 display: flex;
 justify-content: center;
 align-items: center;
 text-align: center;
 flex-direction: column;
}

// Post version
.post-version {
 position: absolute;
 left: 50%;
 transform: translateX(-50%);
 color: gray;
 font-size: 0.875rem;
 text-align: center;

 a {
 color: inherit;
 text-decoration: underline;
 }

 @media print {
 position: static;
 transform: none;
 text-align: center;
 }
}

// Prevent list overflow
ol,
ul,
dl {
 max-width: 100%;
 overflow-wrap: break-word;
}

// Button in Bibliography and Writings
.links {
 a.btn {
 color: var(--global-text-color);
 border: 1px solid var(--global-text-color);
 padding-left: 1rem;
 padding-right: 1rem;
 padding-top: 0.25rem;
 padding-bottom: 0.25rem;
 margin-left: 0;

 &:hover {
 color: var(--global-theme-color);
 border-color: var(--global-theme-color);
 }
 }

 a.award.btn {
 border-color: var(--global-highlight-color);
 }
}
